h1 {
	background: #2980b9;
	color: white;
	margin: 0;
	font-weight: normal;
	padding: 10px 20px;
	text-transform: uppercase;
	font-size: 24px;
}

body {
	font-family: roboto;
	background: #EF3B36;  /* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #FFFFFF, #EF3B36);  /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #FFFFFF, #EF3B36); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.foot{
    text-align: center;
    padding: 10px;
    background: #fff;
}


.add{
    border: none;
    width: 30%;
    padding: 16px 13px 13px 20px;
    background: #8f8b8b;
    color: #000000;
    transition: 0.8s;
}

.add:hover {
    transition: 0.8s;
    color: #8f8b8b;
    background: #000000;
}

li {
	background: #fff;
	/* height: 40px; */
	 line-height: 40px;
    color: #666;
    list-style: none;
}

li:nth-child(2n) {
	background: #f7f7f7;
}

.spn {
	background: #e74c3c;
	height: 40px;
	margin-right: 20px;
	text-align: center;
	color: white;
	width: 0px;
	display: inline-block;
	transition: 0.2s linear;
    opacity: 0;
}

li:hover .spn{
	width: 40px;
	opacity: 1.0;
}

input {
	font-size: 18px;
	color: #2980b9;
	background-color: #d6d6d6;
	width: 70%;
	padding: 13px 13px 13px 20px;
    box-sizing: border-box;
    border: none;
}

input:focus {
    background: #fff;
    border: none;
    outline-color: white;
}

.container {
    padding: 0;
	width: 360px;
	margin: 100px auto;
	background: #f7f7f7;
	box-shadow: 0 0 3px rgba(0, 0, 0, 0.1);

}